import React from 'react'
import { Nav } from 'react-bootstrap'
import {LinkContainer} from 'react-router-bootstrap'

export default function checkoutSteps({step1,step2,step3,step4}) {
  return (
    <Nav className='justify-content-center mb-4'>
      <Nav.Item>
        {step1 ? (
            <LinkContainer to={'/login'}>
                <Nav.Link>Sign In</Nav.Link>
            </LinkContainer>
        ): <Nav.Link disabled className='checkout-signIn'>Sign In</Nav.Link>}
      </Nav.Item>
      <Nav.Item>
        {step2 ? (
            <LinkContainer to={'/login/shipping'}>
                <Nav.Link className='checkout-shipping'>Shipping</Nav.Link>
            </LinkContainer>
        ): <Nav.Link disabled>Shipping</Nav.Link>}
      </Nav.Item> <Nav.Item>
        {step3 ? (
            <LinkContainer to={'/payment'}>
                <Nav.Link className='checkout-payment'>Payment</Nav.Link>
            </LinkContainer>
        ): <Nav.Link disabled>Payment</Nav.Link>}
      </Nav.Item> <Nav.Item>
        {step4 ? (
            <LinkContainer to={'/placeorder'}>
                <Nav.Link className='checkout-place-order'>Place Order</Nav.Link>
            </LinkContainer>
        ): <Nav.Link disabled>Place Order</Nav.Link>}
      </Nav.Item>
    </Nav>
  )
}
